<template>
    <v-container grid-list-md>
        <v-card >
                    <v-card-title primary-title>
                        <h3>Ordenar</h3> 
                    </v-card-title>
                    <v-flex xs12>
                        <v-btn-toggle v-model="text">
                            <v-btn flat value="left" @click="minmax(1)">
                                Precio MIN-MAX
                            </v-btn>
                            <v-btn flat value="center" @click="maxmin(1)">
                                Precio MAX-MIN
                            </v-btn>
                            <v-btn flat value="right" @click="az(1)">
                                A-Z
                            </v-btn>
                            <v-btn flat value="justify" @click="za(1)">
                                Z-A
                            </v-btn>
                        </v-btn-toggle>
                    </v-flex>
                </v-card>
        <br><br>
        <v-layout wrap>
            
            <v-flex sm12>
                <v-layout wrap>
                    <v-flex sm4 v-for="(item, index) of productosState" :key="index">
                        <!--{{index}} - {{item.name}}>-->
                        <v-hover>
                            <v-card 
                            slot-scope="{ hover }"
                            class="mx-auto"
                            color="grey lighten-4"
                            max-width="400"
                            max-height="700px"
                            >
                                <v-img
                                    src="https://cdn.vuetifyjs.com/images/cards/desert.jpg"
                                    aspect-ratio="1.80"
                                >
                                    <v-expand-transition>
                                        <div
                                            v-if="hover"
                                            class="d-flex transition-fast-in-fast-out green darken-2 v-card--reveal display-3 white--text"
                                            style="height: 100%;"
                                        >
                                            ${{item.precio}}
                                        </div>
                                    </v-expand-transition>
                                </v-img>
                                <v-card-title primary-title>
                                    <h3>
                                        {{item.titulo}}
                                    </h3>
                                </v-card-title>
                                <v-card-text  aspect-ratio="0.2">
                                    <p color="green">{{item.descripcion}}</p>
                                    <v-chip color="green" text-color="white">Precio: ${{item.precio}}</v-chip>
                                </v-card-text>
                                
                                <v-card-actions>
                                    <v-btn flat color="green" :to="{name: 'producto', params: {id:item.id}}">Ver detalles</v-btn>
                                    <v-btn 
                                        
                                        color="green"
                                        :to="{name: 'carrito'}"
                                        class="white--text"
                                        
                                        @click="setCarrito(item)"
                                        >
                                        <v-icon >local_grocery_store</v-icon>
                                    </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </v-flex>
        </v-layout>
        
    </v-container>
</template>

<script>

import ToolbarTienda from '../components/ToolbarTienda.vue'
import axios from "axios"
  import {mapMutations, mapState} from "vuex"

export default {
    name:'productos',
    data() {
        return {
            
            productos: [],
            productores:[],
            text:'',
        }
    },
    methods: {
        ...mapMutations(['mostrarLoading', 'ocultarLoading', 'falso', 'setProductos', 'maxmin', 'minmax','az','za', 'inicializaCarrito', 'setCarrito']),
        async getProductos() {
          

          try {
            this.mostrarLoading({
              titulo: 'Accediendo a informacion',
              color: 'secondary'
            })
            axios.create({headers: {'Access-Control-Allow-Origin':'*' }})
            let datos = await axios.get('http://apis-servicio.cernet.com.mx/product/products')
            console.log(datos.data)
            this.setProductos(datos.data)
          }catch (error) {
            console.log(error)
          }finally {
            this.ocultarLoading()
          }
      },
      
    },
    created() {
        this.getProductos();
         this.falso(true)
         //console.log(localStorage.carrito)
         this.inicializaCarrito()
         
    },
    computed : {
        ...mapState(['productosState', 'carrito']),
       
    }
}
</script>

<style>
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .5;
  position: absolute;
  width: 100%;
}
</style>

